.signBasic{
    border-top: 4px solid #44B549;
    background: #fff;
    font-size: 16px;
}
    .signBasic header{
        width: 100%;
        height: 82px;
    }
    .signBasic header .header_inner{
            margin: 0 auto;
            width: 960px;
            height: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    .signBasic header .header_inner .logo{}
    .signBasic header .header_inner .signUpBtn{
                display: flex;
                align-items: center;
            }
    .signBasic header .header_inner .signUpBtn a{
                    border: 0;
                    outline: none;
                }
    .signBasic header .header_inner .signUpBtn a::before{
                    content: '';
                    display: inline-block;
                    vertical-align: middle;
                    width: 17px;
                    height: 16px;
                    margin-bottom: 2px;
                    margin-right: 5px;
                    background: transparent url(/WeChat-2018/build/static/media/sign_signUpIcon.1a0cf7bb.svg) no-repeat center;
                }
    .signBasic main{
        width: 100%;
        height: 312px;
        background: transparent url(/WeChat-2018/build/static/media/signBackgroundImg.e5ae51ee.png) no-repeat center 0;
        background-color: #090b1a;
    }
    .signBasic main .main_inner{
            margin: 0 auto;
            width: 960px;
            height: 100%;
            padding: 22px 0;
            display: flex;
            justify-content: flex-end;
        }
    .signBasic main .main_inner form{
                background: #fff;
                width: 400px;
                height: 272px;
                padding: 20px 35px;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: flex-start;
            }
    .signBasic main .main_inner form h3{
                    font-size: 20px;
                    margin-bottom: 10px;
                }
    .signBasic main .main_inner form .row{
                    width: 100%;
                    height: 50px;
                    position: relative;
                }
    .signBasic main .main_inner form .row i{
                        display: inline-block;
                        vertical-align: middle;
                        font-size: 14px; 
                        color: #cccccc;
                        position: absolute;
                        top: 50%;
                        left: 10px;
                        transform: translateY(-50%);
                    }
    .signBasic main .main_inner form .row input{
                        width: 100%;
                        height: 100%;
                        border: 1px solid #e7e7eb;
                        border-top: 0px;
                        padding: 0 5px 0 35px;
                        font-size: 14px;
                        line-height: 50px;
                        background-color: #ffffff;
                        outline: none;
                        border-radius: 2px;
                        cursor: text;
                    }
    .signBasic main .main_inner form .row input.firstInput{
                        border-top: 1px solid #e7e7eb;
                    }
    .signBasic main .main_inner form .row input:-webkit-autofill{
                        -webkit-box-shadow: 0 0 0 1000px white inset; 
                    }
    .signBasic main .main_inner form .button_wrapper{
                    margin-top: 20px;
                }
    .signBasic main .main_inner form .button_wrapper .button{
                        margin: 0;
                        display: inline-block;
                        vertical-align: top;
                        width: 120px;
                        height: 33px;
                        border-radius: 3px;
                        background: #44b549;
                        color: #fff;
                        border: 0;
                        outline: none;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }
    .signBasic main .main_inner form .button_wrapper .button:hover{
                        filter: brightness(0.9);
                    }
.tooltips{
    width: 400px;
    position: fixed;
    left: 50%;
    top: 0;
    margin-left: -200px;
    text-align: center;
    padding: 10px;
    border-radius: 5px;
    background-color: #aaaaaa;
}
.sb{
    flex-shrink: 0;
    width: 60px;
    height: 100%;
    background: #26292e;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}
    .sb .sb_header{
        width: 100%;
        height: 60px;
        padding-top: 18px;
        text-align: center;
    }
    .sb .sb_header .myNickName{
            color: white;
        }
    .sb .sb_header .photo_wrapper{
            display: inline-block;
            vertical-align: middle;
            width: 35px;
            height: 35px;
        }
    .sb .sb_header .photo_wrapper img{
                width: 100%;
            }
    .sb .sb_main{
        padding-top: 18px;

    }
    .sb .sb_main .icon_wrapper{
            width: 34px;
            height: 34px;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;
        }
    .sb .sb_main .icon_wrapper .icon-chat{
                font-size: 30px;
                color: #0db905;
            }
    .sb .sb_footer{
        position: absolute;
        bottom: 15px;
    }
    .sb .sb_footer .icon_wrapper{
            width: 34px;
            height: 34px;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;
        }
    .sb .sb_footer .icon_wrapper .icon-shezhi{
                font-size: 30px;
                color: #8f9297;
            }




.bp{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background: #f6f6f6;
}
    .bp .bp_header{
        flex-shrink: 0;
        width: 100%;
        height: 60px;
        line-height: 60px;
        text-align: center;
        font-size: 16px;
        border-bottom: 1px solid #dfdfdf;
    }
    .bp .bp_main{
        flex-grow: 1;
        overflow: auto;
        width: 100%;
    }
    .bp .bp_main .timeHint{
            width: 100%;
            height: 15px;
        }
    .bp .bp_footer{
        flex-shrink: 0;
        width: 100%;
        height: 130px;
        
    }
    .bp .bp_footer textarea{
            max-width: 100%;
            min-width: 100%;
            max-height: 100%;
            min-height: 100%;
            width: 100%;
            height: 100%;
            padding: 12px 26px 38px 26px;
            border: 1px solid #e1e1e1;
            outline: none;
        }
    .bp .bp_footer .sendButton{
            border: 1px solid #f6f6f6;
            height: 24px;
            line-height: 24px;
            padding: 0 14px;
            font-size: 14px;
            border-radius: 3px;
            background: #f0f0f0;

            position: absolute;
            right: 30px;
            bottom: 10px;
        }
    .bp .bp_footer .sendButton::after{
            content: '';
            display: inline-block;
            width: 200%;
            height: 200%;
            border: 1px solid #c2c2c2;
            transform: scale(.5);
            transform-origin: left top;

            position: absolute;
            top: 0;
            left: 0;
        }
    .mi_item{
    padding: 0 30px;
    margin-bottom: 16px;
    display: flex;
    align-items: flex-start;
}
    .mi_item .mi_photo{
        flex-shrink: 0;
        width: 34px;
        height: 34px;
        margin-right: 10px;
    }
    .mi_item .mi_photo img{
            width: 100%;
        }
    .mi_item .mi_text{
        flex-shrink: 0;
        max-width: 215px;
        line-height: 30px;
        border: 0 solid #e1e1e1;
        padding: 1px 10px;
        background: #ffffff;
        font-size: 16px;
        word-wrap: break-word;
        position: relative;
    }
    .me{
    justify-content: flex-end;
}
    .me .mi_text{
        color: #ffffff;
        background: #0db905;
    }
    .me .mi_text .san{
            border: 7px solid #0db905;
            border-top-color: transparent;
            border-bottom-color: transparent;
            border-right-color: transparent;
            position: absolute;
            right: -13px;
            top: 0;
        }
    .you{
    justify-content: flex-start;
}
    .you .mi_text{
        color: #000;
        background: #ffffff;
    }
    .you .mi_text .san{
            border: 7px solid #ffffff;
            border-top-color: transparent;
            border-bottom-color: transparent;
            border-left-color: transparent;
            position: absolute;
            left: -13px;
            top: 0;
        }
    .me .mi_photo{
    order: 1;
    margin-left: 15px;
}
    .you .mi_photo{
    order: 0;
    margin-right: 15px;
}
.dlg_wrapper{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.dlg_wrapper > .dlg_mask{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0,0,0,.3);
}
.dlg_wrapper > .dlg{
    width: 360px;
    height: 240px;
    padding: 10px;
    position: relative;
    background: #dddddd;
}
.dlg_wrapper > .dlg > .dlg_closeBtn{
    display: inline-block;
    padding: 3px 5px;
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}
.dlg_wrapper > .dlg > .dlg_header{
    font-size: 16px;
    margin: 30px 0;
    text-align: center;
}
.dlg_wrapper > .dlg > .dlg_main{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.dlg_wrapper > .dlg > .dlg_footer{
    position: absolute;
    bottom: 10px;
    left: 10px;
    right: 10px;
    text-align: right;
}



